.menubar ul{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    background-color: rgb(255,255,255) !important;
    margin-bottom: 0%;
    padding-left: 0% !important;
}
.menubar ul li{
    list-style: none;
    text-decoration: none;
    height: 32px;
    display: flex;
    background-color: rgb(255,255,255) !important;
    align-items: center;
    justify-content: center;
    border: 1px solid #E0E0E0;
    
}
.navlink{
    text-decoration: none;
    color:#494848;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.2em
}
.uploadsub,#uploadcenter{
    position: relative;
}
#uploadsub .submenu{
    width: inherit;
    position: absolute;
    display: flex;
    flex-direction: column;
    margin-top: 130px;
}
#uploadsub .submenu  ul li{
    width: 100%;
    list-style: none;
    text-decoration: none;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #E0E0E0;
}
.navlinks{
    width: 100%;
}
.nav-link{
    text-decoration: none;
}
.uactive{
    font-weight: 700;
}
@media (max-width:769px) {
    .menubar{
        display: none;
    }
}