.containers {
  padding-top: 150px;
  margin-bottom: 100px;
}
.topart {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-left: 5%;
}

.topart .salogans {
  width: 40%;
}
.topart .salogans h6 {
  font-size: 18px;
  font-weight: 300;
  color: #000000;
  padding-left: 80px;
}
.headig {
  text-align: center;
  padding: 20px 0;
  margin-top: 20px;
  border-top: 0.5px solid #9c9c9c;
}
.headig h1 {
  font-size: 18px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.topart .prfile {
  width: 60%;
}

.topart .prfile h2 {
  font-size: 24px;
  font-weight: 300;
  padding-top: 20px;
  letter-spacing: 2px;
}
.topart .prfile img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
}
.prfiles {
  display: flex;
  justify-content: space-evenly;
  padding-left: 3%;
  flex-wrap: wrap;
  row-gap: 2.5rem;
}
.prfiles .save1 {
  width: 400px;
  height: 400px;
}
.prfiles .save1 img {
  width:100%;
  height: 100%;
}
