.editor {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3rem 0;
  }
  .editor .inner {
    width: 100%;
    height: auto;
  }
  .jodit .jodit-workplace .jodit-wysiwyg, .jodit .jodit-workplace .jodit-wysiwyg_iframe, .jodit-container .jodit-workplace .jodit-wysiwyg, .jodit-container .jodit-workplace .jodit-wysiwyg_iframe{
    background-color: #ffffff;
  }
  .jodit .jodit-workplace .jodit-wysiwyg, .jodit .jodit-workplace .jodit-wysiwyg_iframe, .jodit-container .jodit-workplace .jodit-wysiwyg, .jodit-container .jodit-workplace .jodit-wysiwyg_iframe{
      height: 350px !important;
  }
  .editor #btn {
    margin-top: 47px;
    width: 84px;
    height: 38px;
    cursor: pointer;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    outline: none;
    background:  #4F4F4F;
    border-radius: 3px;
    text-transform: capitalize;
    align-self: baseline;
  }
  .jodit-toolbar-editor-collection_size_middle.jodit-toolbar-editor-collection_mode_horizontal {
    border: none !important;
  }
  .editor .inner .shift {
    background: #ffffff;
    border: 1px solid #000000;
    border-radius: 3px 3px 0px 0px;
    padding: 0.1rem 1rem;
    font-family: Arial;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0.1em;
    color: #494848;
    margin-right: 1rem;
    border-bottom: none;
  }
  .jodit .jodit-workplace .jodit-wysiwyg,
  .jodit .jodit-workplace .jodit-wysiwyg_iframe,
  .jodit-container .jodit-workplace .jodit-wysiwyg,
  .jodit-container .jodit-workplace .jodit-wysiwyg_iframe {
    border: none !important;
  }
  .jodit-container:not(.jodit_inline) {
    border: 1px solid #000000 !important;
}