*{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}
.hero{
    background-color: #FFFFFF;
    padding-left: 50px;
    height: 700px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    clip-path: polygon(100% 61%,100% 0,0 0,0 99%);
}
.hero h6{
font-family: 'Calibri';
font-style: normal;
font-weight: 300;
font-size: 18px;
color: #333333;
}
.hero h1{
    font-family: 'Times New Roman';
font-style: normal;
font-weight: 400;
font-size: 36px;
color: #C6A072;
margin: 30px 0 20px 0;
}
.hero p{
    width: 575px;
    font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
color: #333333;
margin-bottom: 20px;
}
.discover{
    display: flex;
    height: 40px;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 100px;
}
.discover a{
    font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 20px;
text-decoration: none;
color: #3E3E3E;
cursor: pointer;
}
.discover svg{
    margin-left: 20px;
    z-index: 1;
    cursor: pointer;
}
.discover .browncircle{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color:  #E4BA86;
    margin-left: -20px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.discover .browncircle:hover{
    width: 150px;
    height: 150px;
    margin-left: 0;
}
.discover .browncircle:hover .circlemenu{
    display: flex;
}
.discover .browncircle .circlemenu{
    /* width: 300px; */
    width: 100%;
    margin: auto;
    border-radius: 50%;
    background-color:  #E4BA86;
    display: flex;
    justify-content: center;
    display: none;
}
.discover .browncircle .menufaq{
    border-right: 1px solid white;
    width: 25%;
}
.discover .browncircle .menufaq .circlelink{
    color: white;
    font-size: 14px;
    font-weight: 600;
     text-transform: uppercase;
     height: 100%;
     display: flex;
     align-items: center;
     justify-content: center;
}
.discover .browncircle .menugallery .circlelink{
    color: white;
    font-size: 12px;
    font-weight: 400;
     text-transform: uppercase;
     height: 40px;
     display: flex;
     align-items: center;
     padding-left: 15px;
}
.discover .browncircle .menugallery .circlelink:nth-child(2){
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}
.discover .browncircle .circlemenu .menugallery{
    display: flex;
    flex-direction: column;
    width: 75%;
}
.maps{
    background-color: #494848;
    clip-path: polygon(100% 100%, 100% 0, 0 30%, 0 100%);
     margin-top: -300px;
     padding-bottom: 80px;
}
.maps .mapimage{
    text-align: right;
}
.maps img{
    padding-top: 250px;
}
.maps .maptextsect{
    padding-left: 50px;
    margin-top: -100px;
}
.maps .maptextsect h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #F2F2F2;
}
.maps .maptextsect h2{
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    color: #C6A072;
    margin: 20px 0 30px 0;
}
.maps .maptextsect p{
    width: 530px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #F2F2F2;
}
.maps .mtextpart{
    display: flex;
    justify-content: flex-end;
    padding-right: 100px;
    margin-top: 50px;
}
.maps .mtextpart p{
    width: 530px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #F2F2F2;   
}
.eeveestablish{
    height: 830px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #FFFFFF !important;
}
.eeveestablish .eeveouter{
     width: 830px;
     height: 530px;
    position: relative;
     border: 2px solid #C6A072;

}
.eeveestablish .eeveouter .eeveiner{
    width: 830px;
    height: 530px;
    background-color: #E5D5C3;
    padding-left: 50px;
    position: absolute;
    top: 40px;
    left: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 2px 10px 2px #CAC8CC;    ;
}
.eeveestablish .eeveouter .eeveiner h2{
    max-width: 530px;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    color: #af8859;
    margin: 15px 0 35px 0;
}
.eeveestablish .eeveouter .eeveiner h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #4F4F4F;

}
.eeveestablish .eeveouter .eeveiner p{
    max-width: 530px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #4F4F4F;
    margin-bottom: 20px;
}
/* testimonial start */
.testmonial{
    height: 810px;
    display: flex;
    align-items: center;
    background-color: #494848;
}
.testmonial .quotes{
    height:460px;
}
.testmonial .testmonialtext{
    padding-left: 20px;
    padding-right: 30px;
    width: 769px;
}
.testmonial .testmonialtext h3{
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 34px;
    color: #C6A072;
    margin-bottom: 24px;
}
.testmonial .testmonialtext p{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #F2F2F2;
    margin-bottom: 25px;
    line-height: 27px;
}
.testmonial .testmonialtext h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #C6A072;
    text-align: right;
    margin-top: 20px;
}
.testmonial .testmonialpara{
    width: 450px;
    height: 170px;
    background-color: #E5D5C3;
    margin-left: 50px;
    padding: 0px 36px;
    display: flex;
    align-items: center;
    margin-top: -100px;
}
.testmonial .testmonialpara p{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #333333;
    margin-bottom: 0;
}
/* feature start */
.features{
    height: 670px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #F5F5F5;
    padding-left: 50px;
}
.features .featuretext h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #333333;
}
.features .featuretext h2{
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 34px;
    color: #C6A072;
    margin: 10px 0 25px;
}
.features .featuretext p{
    max-width: 655px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #333333;
}
.features .featuresexp{
    display: flex;
    justify-content: space-between;
    padding: 80px 50px 0 0px;
}
.features .featuresexp .featurename{
    display: flex;
}
.features .featuresexp .featurename p{
    max-width: 250px;
    padding-left: 20px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #333333;
}

/* feature end */
.customerchoice{
    padding: 150px 50px;
    background-color: #FFFFFF !important;

}
.customerchoice .customertextcont{
    display: flex;
    justify-content: flex-end;
}
.customerchoice .customertextcont .customerchoicetext{
    width: 670px;
}
.customerchoice .customertextcont .customerchoicetext h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #4F4F4F;
}
.customerchoice .customertextcont .customerchoicetext h2{
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 34px;
    color: #C6A072;
    margin: 15px 0 25px;
}
.customerchoice .customertextcont .customerchoicetext p{
    max-width: 540px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #333333;
    margin-bottom: 20px;
}
.customerchoice .customertextcont .customerchoicetext p:last-child{
    margin-bottom: 0;
}
.customerchoice .choices{
    display: grid;
    grid-template-columns: auto auto auto auto;
    position: relative;
    margin-top: -150px;
}
.customerchoice .choices .choice{
    width: 310px;
    height: 315px;
    border: 2px solid #C6A072;
    padding: 20px 24px;
}
.customerchoice .choices .choice h3{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    color: #333333;
    margin-bottom: 10px;
}
.customerchoice .choices .choice p{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #333333;
    line-height: 24px;
}
.customerchoice .choices .choice:nth-child(2){
    height: 240px;
  margin-top: 150px;
}
.customerchoice .choices .choice:nth-child(3){
    height: 210px;
  margin-top: 300px;
}
.customerchoice .choices .choice:nth-child(4){
    height: 175px;
  margin-top: 450px;
}
.customerchoice .individual h1{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 100px;
    color: rgba(202, 200, 204, 0.3);
    text-transform: uppercase;
}
/* customer end */
/* contact start */
.contactus{
    padding: 100px 0;
    background-color: #494848;
}
.contactus .contact .formcontact h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: #BDBDBD;
}
.contactus .contact .formcontact{
    width: 560px;
    margin: auto;
    padding-top: 100px;
}
.contactus .contact .formcontact h2{
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 34px;
    color: #C6A072;
    max-width: 411px;
    margin: 15px 0 25px 0;
}
/* .contactus .contact .formcontact form{
    display: grid;
} */
.contactus .contact .formcontact form .texrow{
    width: 556px;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
.contactus .contact .formcontact form .texrow .texinput{
    width: 250px;
    height: 40px;
}
.contactus .contact .formcontact form .texrow .texinput input{
    width: inherit;
    height: inherit;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #4F4F4F;
    padding-left: 10px;
    background-color: #FFFFFF !important;
}
.contactus .contact .formcontact form .messinput{
    width: 556px;
    margin-top: 20px;
}
.contactus .contact .formcontact form .messinput textarea{
    width: inherit;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #4F4F4F;
    padding-left: 10px;
    padding-top: 10px;
}
#getintouch{
    cursor: pointer;
}
#getintouch a{
    color: #FFFFFF;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    cursor: pointer;
}
.contactus .contact .frameimg{
    position: absolute;
    left: 23%;
}
#mobileestablish{
    display: none;
}
/* mobile view */

@media (max-width:769px) {
    .hero{
        padding-left: 25px;
        padding-right: 25px;
        padding: 35px 25px 0 25px;
        clip-path: none;
    }
    .hero h1{
        font-size: 32px;
        margin-top: 6px;
    }
    #homeheaddash{
        display: none;
    }
    #landindustry{
        margin-top: 10px;
        padding-top: 10px;
    }
    .hero p,.maps .maptextsect p,.maps .mtextpart p,.eeveestablish .eeveouter .eeveiner p,.features .featuretext p,.customerchoice .customertextcont .customerchoicetext p{
     font-size: 16px;
     max-width: 100%;
    }
    .discover{
        margin-top: 30px;
    }
    .maps{
        clip-path: polygon(100% 100%, 100% 0, 0 10%, 0 100%);
        margin-top: -100px;
        padding: 0 25px;
    }
    .maps img{
        max-width: 100%;
        height: 382px;
        padding-top: 150px;
        overflow-x: hidden;
    }
    .maps .maptextsect{
        padding-left: 0%;
        margin-top: 60px;
    }
    .maps .maptextsect h6{
        font-size: 16px;
    }
    .maps .maptextsect h2{
        font-size: 24px;
        margin-top: 8px;
    }
    .maps .mtextpart{
        margin-top: 0;
        padding-right: 0%;
        padding-bottom: 75px;
    }
    .eeveestablish .eeveouter{
        margin-right: 15px;
        height: 500px;
    }
    .eeveestablish .eeveouter .eeveiner{
        width: 100%;
        height: auto;
        left: 12px;
        top: 12px;
        padding: 30px 20px 30px 25px;
    }
    .eeveestablish .eeveouter .eeveiner h6{
        font-size: 16px;
    }
    .eeveestablish .eeveouter .eeveiner h2{
        font-size: 24px;
    }
    #mobileestablish{
        display: block;
    }
    #desktopestab{
        display: none;
    }
    .testmonial .testmonialtext{
        max-width: 100%;
        padding-top: 30px;
    }
    .testmonial{
        height: auto;
        flex-direction: column;
        padding: 60px 25px;
    }
    .testmonial .quotes{
        height: auto;
        align-self: flex-start;
    }
    .testmonial .quotes img{
        max-width: 50%;
    }
    .testmonial .testmonialtext h3,.features .featuretext h2,.customerchoice .customertextcont .customerchoicetext h2{
        font-size: 24px;
    }
    .testmonial .testmonialtext p,.testmonial .testmonialtext h6,.testmonial .testmonialpara p,.features .featuretext h6,.features .featuresexp .featurename p,.customerchoice .customertextcont .customerchoicetext h6{
        font-size: 16px;
        text-align: left;
    }
    .testmonial .testmonialpara{
        margin-top: 50px;
        padding: 24px 30px;
        max-width: 93.5vw;
        margin-left: 25px;
    }
    .features{
        height: auto;
        padding: 80px 25px;
    }
    .features .featuresexp{
        flex-direction: column;
        padding: 36px 50px 0 0;
    }
    .customerchoice{
        padding: 80px 25px;
    }
    .customerchoice .choices{
        margin-top: 30px;
        grid-template-columns: 1fr;
    }
    .customerchoice .choices .choice{
        width: 100%;
        height: auto;
        padding: 24px;
    }
    .customerchoice .choices .choice:nth-child(2),.customerchoice .choices .choice:nth-child(3),.customerchoice .choices .choice:nth-child(4){
        margin-top: 35px;
        height: auto;
    }
    .customerchoice .choices .choice h3{
        font-size: 20px;
    }
    .customerchoice .individual h1{
        display: none;
    }
    .contactus{
        padding: 70px 25px 40px 25px;
    }
    .contactus .contact .frameimg{
        display: none;
    }
    .contactus .contact .formcontact{
        padding-top: 0%;
        width: 100%;
    }
    .contactus .contact .formcontact h6{
        font-size: 16px;
    }
    .contactus .contact .formcontact h2{
        font-size: 24px;
    }
    .contactus .contact .formcontact form .texrow{
        width: 100%;
        flex-direction: column;
        margin-top: 0%;
    }
    .contactus .contact .formcontact form .texrow .texinput{
        width: inherit;
        margin-top: 24px;
    }
    .contactus .contact .formcontact form .messinput{
        width: 100%;
        margin-top: 24px;
    }
    .contactus .contact .formcontact form .texrow .texinput input{
        background-color: #FFFFFF !important;
    }
    
}
