.sidemnu{
    width: 300px;
    height: 100vh;
    background-color: white;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 10;
    display: none;
}

.sidemnu h6{
    padding-top: 20px;
    font-size: 28px;
    padding-left: 30px;
    cursor: pointer;
}
.sidemnu ul{
    background-color: rgb(255,255,255,);
    padding-top: 30px;
}
.sidemnu ul li{
    list-style: none;
    text-decoration: none;
    height: 52px;
    display: flex;
    padding-left: 30px;
    align-items: center;
    /* border: 1px solid #E0E0E0; */
}
.sidelink{
    text-decoration: none;
    color:#494848;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16p;
    text-transform: uppercase;
    letter-spacing: 0.2em
}