@font-face {
    font-family: Manrope;
    src: url(../../font/Manrope-Regular.ttf);
  }
.forumsect{
    padding: 50px 40px;
    background-color: #f4f4f4;
    display: flex;
}
.forumsect .forumlsec {
    width: 20%;
}
.forumsect .forumlsec h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #828282;
}
.forumsect .forumlsec .forumfilters .filters{
    width: 100%;
    margin-top: 12px;
}
.forumsect .forumlsec .forumfilters .filters select{
    width: 100%;
    height: 35px;
    background-color: #ffffff;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #212121;
    outline: none;
    padding: 0 10px;
}
.forumsect .forumlsec .forumbutns{
    margin-top: 50px;
}
.forumsect .forumlsec .forumbutns .fbtns{
    margin-top: 15px;
}
.forumsect .forumlsec .forumbutns .fbtns button{
   width: 100%;
   height: 38px;
   font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #4F4F4F;
    background: #FAFAFA;
    border: 1px solid #4F4F4F;
}
.forumsect .forumlsec .forumbutns .fbtns:last-child button{
    background-color: #4F4F4F;
    color: #FFFFFF;
}
/* right sec */
.forumsect .forumrsec{
    width: 80%;
    padding-left: 35px;
}
.forumsect .forumrsec .forumtabs{
    display: flex;
    width: 100%;
}
.forumsect .forumrsec .forumtabs .tab{
    width: 25%;
}
.forumsect .forumrsec .forumtabs .active-ftabs button{
    background-color: #E0E0E0 !important;
}
.forumsect .forumrsec .forumtabs .tab button{
    width: 100%;
    height: 32px;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #494848;
    background-color: rgba(255, 255, 255, 0.5);
    border: none;
    border-right: 1px solid #E0E0E0;
}

/* gview */
#savedposts{
    margin: 30px 0;
}
.ftabs{
    display: none;
}
.active-ftabscontent{
    display: block;
}
.questiondisc h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 27px;
    color: #828282;
    margin: 30px 0 0px 0;
}
.questiondisc p{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 37px;
    color: #494848;
}
.questiondisc .discfilter{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.questiondisc .discfilter .tans h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #494848;
    margin:0 !important;
} 
.questiondisc .discfilter .tfilter{
   display: flex;
}
.questiondisc .discfilter .tfilter .finput{
    width: 200px;
    margin-left: 32px;
}
.questiondisc .discfilter .tfilter .finput input,.questiondisc .discfilter .tfilter .finput select{
    width: 100%;
    height: 35px;
    background-color:  #ffffff !important;
    border: 1px solid #F2F2F2;
    padding: 0 16px;
}
.questiondisc .discfilter .tfilter .finput button{
    width: 136px;
    height: 38px;
    background: #4F4F4F;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
}

/* answer */

.fanswer{
    background-color: #ffffff;
    width: 100%;
    border: 0.3px solid #F2F2F2;
    border-radius: 6px;
    margin-top: 30px;
    padding-bottom: 25px;
}
.fanswer .fanshead{
    display: flex;
    justify-content: space-between;
    padding: 25px 24px 0px 24px;
}
.fanswer .fanshead .fprofile{
    display: flex;
    height: 50px;
    align-items: center;
}
.fanswer .fanshead .fprofile .fimg img{
    width: 50px;
    height: 50px;
}
.fanswer .fanshead .fprofile .fname{
    padding-left: 16px;
    display: flex;
    height: 20px;
    align-items: center;
}
.fanswer .fanshead .fprofile .fname h5{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    margin-right: 10px;
    color: #494848;
    margin-bottom: 0;
}
.fanswer .fanshead .fprofile .fname span{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    color: #828282;
    margin-right: 8px;
}
.fanswer .fbody{
    padding: 0 24px 0 88px;
}
.fanswer .fbody .qa p{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #494848;
    margin-top: -10px;
}
.fanswer .fbody .ffooter{
    display: flex;
    margin-top: 25px;
}
.fanswer .fbody .ffooter .icontext{
    display: flex;
    height: 23px;
    align-items: center;
    width: 50px;
}
.fanswer .fbody .ffooter .icontext:nth-child(3){
    justify-content: center;
}
.fanswer .fbody .ffooter .icontext:last-child{
    width: 80px;
    padding-left: 10px;
}
.fanswer .fbody .ffooter .icontext p{
    font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
color: #494848;
margin-bottom: 0;
margin-left: 5px;
}


/* ansreply */
#ansreply{
    background-image: url('../../img/forum.png') !important;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: transparent;
    padding-top: 15px;
}
.replyanswe{
    margin-left: 40px;
}
.questiondisc .morereplybtn {
    width: 100%;
    margin: 15px 0;
}
.questiondisc .morereplybtn button{
    width: 100%;
    height: 42px;
    border: 1px solid #828282;
    background-color: transparent;
    text-align: left;
    padding-left: 24px;
}
.questiondisc hr{
    background-color: #515151;
    height: 2px;
    margin: 35px 0;
}

/* pagination */
.questiondisc .pagination{
    margin-top: 46px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.questiondisc .pagination .previous{
   padding-right: 10px;
   cursor: pointer;
}
.questiondisc .pagination .next{
    padding-left: 10px;
    cursor: pointer;
}
.questiondisc .pagination .pagenum{
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 50%;
    margin: 0 6px;
    cursor: pointer;
}
.questiondisc .pagination .pagenum:nth-child(3){
    background-color: #4F4F4F;
    color: #ffffff;
}

#chtheme{
    display: none;
}


.mobiletabs .forumtabs{
    display: none;
    overflow-x: auto;
}
.mobiletabs .forumtabs::-webkit-scrollbar{
    width: 0px;
    background-color: #f4f4f4;
}
.mobiletabs .forumtabs .tab{
    width: 250px;
    height: 32px;
}
.mobiletabs .forumtabs .active-ftabs button{
    background: #E0E0E0 !important;
}
.mobiletabs .forumtabs .tab button{
    width: 250px;
    height: 32px;
    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #494848;
    background-color: rgba(255, 255, 255, 0.5);
    border: none;
    border-right: 1px solid #E0E0E0;
}

.popsmain{
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    display: none;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
}
.themes-popup{
    width: 90%;
    background-color: #f4f4f4;
    padding: 20px 33px 60px 32px;
}
.themes-popup h6{
    display: block !important;
}
.themes-popup h5{
    color: #000000;
    font-size: 30px;
    text-align: right;
}
.themes-popup .forumfilters .filters button{
    width: 100%;
    height: 38px;
    background-color: #4F4F4F;
    color: #ffffff;
}
/* mobile */
@media (max-width:769px) {
    .mobiletabs .forumtabs{
        display: flex;
    }
    .forumsect{
        flex-direction: column;
        padding: 30px 20px;
    }
    .forumsect .forumlsec{
        width: 100%;
    }
    .themes-popup .forumfilters{
        display: flex !important;
        flex-direction: column;
    }
    .forumsect .forumlsec h6{
        display: none;
    }
    #chtheme{
        display: block;
    }
    .forumsect .forumlsec button{
        width: 100%;
        height: 38px;
        font-family: 'Calibri';
         font-style: normal;
         font-weight: 400;
         font-size: 18px;
         line-height: 22px;
         color: #4F4F4F;
         background: #FAFAFA;
         border: 1px solid #4F4F4F; 
         text-align: center;
         display: block;
    }
    .forumsect .forumlsec .forumfilters {
        display: none;
    }
    .forumsect .forumlsec .forumbutns{
        margin-top: 0;
    }
    .forumsect .forumrsec .forumtabs{
        display: none;
    }
    .forumsect .forumrsec{
        width: 100%;
        padding: 0 0px;
    }
    .questiondisc p{
        font-size: 24px;
    }
    .questiondisc .discfilter,.questiondisc .discfilter .tfilter{
        flex-direction: column;
    }
    
    .questiondisc .discfilter .tfilter .finput{
        width: 100%;
        margin-left: 0;
        margin-top: 16px;
    }
    .questiondisc .discfilter .tfilter .finput button{
        width: 100%;
    }
    .fanswer .fanshead .fprofile{
        flex-direction: column;
        align-items: flex-start;
    }
    .fanswer .fbody{
        padding: 0 0 0 20px;
    }
    .fanswer .fbody .qa{
        margin-top: 40px;
    }
    .fanswer .fbody .qa p{
        margin-top: 0;
        line-height: 27px;
    }
    .fanswer .fanshead .fprofile .fname{
        padding-left: 0;
        margin-top: 15px;
    }
    .replyanswe{
        margin-left: 0;
    }
    .questiondisc .morereplybtn{
        display: none;
        margin-bottom: 25px;
    }
    .questiondisc .pagination{
        margin-bottom: 30px;
    }
}