.regpage{
 background-color: #f4f4f4;
}
.register {
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
  padding: 100px 0;
}
.register .register_heading{
  font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 36px;
color: #494848;
text-align: center;
}
.register_form {
  margin-top: 3%;
}

.reg_boxs {
  display: flex;
  justify-content: space-between;
  justify-content: space-around;
}
.input_box_register input {
  border: none !important;
  border-bottom: 1px solid black !important;
  outline: none;
  width: 360px;
}
.input_box_register label {
  font-family: Times New Roman;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 80px;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  text-align: center;

  margin-top: 15px;
}
.genders {
  padding-left: 10%;
}
@media (max-width: 870px) {
  .reg_boxs {
    display: block;
    margin-left: 0%;
  }
  .genders{
    text-align: left;
    padding-left: 0%;
  }
  .input_box_register{
    margin-left:  0px !important;
    margin-right: 0% !important;
    width: 100% !important;
  }
  .input_box_register label{
    line-height: normal;
    font-size: 14px;
    letter-spacing: normal;
  }
  .input_box_register input{
    width: 100%;
  }
  .input_box_register input[type="text"]{
    width: 100% !important;
  }
  #register2{
    margin-top: -20px;
  }
  register_heading{
    font-size: 16px;
  }
  .reg_btn_area{
    width: 100% !important;
  }
  .reg_already_text{
    width: 100%;
    font-size: 14px !important;
    line-height: normal !important;
    padding-top: 20px;
    padding-bottom: 10px;
    flex-direction: column;
  }
  .reg_already_text span .links{
     margin-top: 10px;
     font-size: 14px !important;
  }
  .reg_btn_area p span{
     padding-top: 20px;
  }
  #registerlogin{
    padding-top: 10px;
    margin-top: 10px;
  }
}

.input_box_register {
  width: 30%;
  margin: 0 auto;
  justify-content: space-between;
}

.input_box_register input[type="text"] {
  background: #ffffff;
  box-sizing: border-box;
  border-radius: 3px;
  width: 360px;
  height: 32px;
  margin: 5px auto;
}



.reg_btn_area {
  width: 40%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.reg_btn_area .submit {
  width: 300px;
  height: 45px;
  margin-top: 50px;
  margin-left: 0px;
  background: #f4f4f4;
  border-radius: 3px;
  letter-spacing: 0.5em;
  color: #494848;
  font-family: Times New Roman;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  text-align: center;
}

.reg_already_text {
  margin: 0px auto;
  text-align: center;
  justify-content: space-between;
  align-items: flex-end;
  letter-spacing: 0.2em;
  font-family: Times New Roman;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 80px;
  text-transform: uppercase;
}

.reg_already_text span .links {
  color: #2c2c2c;
  border-bottom: 1px solid #494848;
  text-decoration: none;
  font-size: 18px;
}
