.cadup{
    width: 70%;
    margin: 50px auto;
}
body{
    background-color: #f4f4f4 !important;
}
.cadpreview{
    display: grid;
    grid-template-columns: 1fr 1fr 0.1fr 1fr;
    justify-content: space-between;
    border-bottom: 0.3px solid #b7b6b6;
    padding-bottom: 30px;
    margin-bottom: 60px;
}
.previwimg .previmginner{
    background-color: white;
    height: 300px;
}
#uploadupmain{
    object-fit: cover;
}
#uploadlist img{
    object-fit: cover;
}
.cadpreview .previwimg {
    display: flex;
    flex-direction: column;
}
.cadpreview .previwimg .previmg{
    width: 300px;
    text-align: center;
}
.cadpreview .previwimg .previmg img{
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.cadpreview .previwimg .previmg .addshadow{
    box-shadow: 0 0 5px rgba(0,0,0,0.7);
}
.cadpreview .previwimg .previmg h3{
    font-family:'Times New Roman', Times, serif;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.1em;
    text-align: center;
    margin-bottom: 10px;
}
.pendulum .circle{
    width: 15px;
    height: 15px;
    border: 1px solid black;
    border-radius: 50%;
    margin: auto;
}
.pendulum .leftpend{
    transform: rotateZ(35deg) translateX(-60px) translateY(18px);
}
.pendulum .rightpend{
    transform: rotateZ(-35deg) translateX(56px) translateY(18px);
}
.pendulum img {
    margin-top: 10px;
    /* width: 100px; */
    height: 250px;

    /* object-fit: cover; */
}
.cadpreview .previwimg .cadinput{
    display: flex;
    margin-top: 20px;
    align-items: center;
}
.cadpreview .previwimg .cadinput select{
    width: 300px;
    padding: 5px 10px;
    outline: none;
}
.cadpreview .previwimg .cadinput .caddelete{
    width: 28px;
    height: 28px;
    display: flex;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    border: 0.3px solid rgba(0,0,0,0.7);
    margin-left: 10px;
}
.cadpreview .prevbutn1{
    text-align: center;
    align-self: flex-end;
    padding-bottom: 70px;
}
.cadpreview .prevbutn1 button{
width: 190px;
height: 85px;
padding: 0px 10px;
font-family: Arial;
font-size: 16px;
font-weight: 400;
letter-spacing: 0.1em;
text-align: center;
color: #494848;
background-color: #ffffff;
border: 0.3px solid #b7b6b6;
}
.cadpreview .prevbutn1 button span{
    font-family: Arial;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.1em;
    text-align: center;
    color: #000000;
}
.cadpreview .prevbutn1 button hr{
    margin: 05px 0;
}
/* cad preview end */

#cadupload{
    padding-top: 0%;
}

#cadqinput{
    height: 24px;
    box-shadow: none;
    outline: none;
    border-width: 0.7px;
}
#divcadqinput{
    padding-right: 05px;
}
#cadquestin{
    justify-content: center;
}
#cadset{
    padding-left: 0rem;
}
#cadquestionin{
    padding-left: 0%;
}
#cadquestionin{
    padding-left: 2rem !important;
}

@media (max-width:769px) {
    .cadup{
        width: 90%;
    }
    .cadpreview .prevbutn1,.pendulummain,.pendulum{
        display: none;
    }
}