.gallery_main{
  width: 70%;
  margin: 50px auto;
}
#partup{

  background-color: #f4f4f4;
}
.gallerypart{
  display: flex;
}
.allimages .row1 .col1 {
  position: relative;
}
.gallery_main .gallery{
  justify-content: space-between;
}
.btn_div{
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0px;
  margin-bottom: 5rem;
}
#btn {
  margin-top: 47px;
  width: 84px;
  height: 38px;
  cursor: pointer;
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #ffffff;
  outline: none;
  background:  #4F4F4F;
  border-radius: 3px;
  text-transform: capitalize;
  align-self: baseline;
  text-align: center;
}

.loader{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 99999;
  transition: all .2s ease;
  display: none;
}
.loader_active{
  display: flex;
  align-items: center;
  justify-content: center;
}


.allimages .row1 .col1 span{
  position: absolute;
  background-color: rgba(0, 0, 0, 0.65);
  width: 25px;
  height: 25px;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.866);
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 10px;
  right: 5px;
}

