  @media (max-width:769px) {
    .gallery_main{
      width: 90%;
    }
    
    .gallery_main .gallerypart{
      flex-direction: column;
    }
    .gallery_main .gallery .left{
      width: 100%;
      padding: 0%;
    }
    .gallery_main .gallery .left .image img{
      width: 100%;
    }
    #partuploadbtn{
      width: 98%;
    }
    .gallery_main .gallery .center{
      width: 100%;
      height: auto;
      justify-content: none !important;
    }
    .allimages .row1{
      grid-template-columns: repeat(2, 1fr);
    }
    .allimages .row1 .col1 img, .allimages .row1 .col2 img, .allimages .row1 .col3 img, .allimages .row1 .col4 img{
      width: 150px;
      height: 150px;
    }
    .uplcenter .uploadedimg{
      width: 100% !important;
      grid-template-columns: repeat(2,1fr) !important;
      grid-gap: 20px;
      margin-top: 20px;
    }
    .uplcenter{
      flex-direction: column;
    }
    .uplcenter .imageupload{
      width: 100% !important;
    }
    .uplcenter .uploadedimg .uplsecimg{
      height: 150px !important;
      margin-top: 5px;
    }
    .uploadset .inerset .left .categ{
      flex-direction: column;
    }
    .uploadset .inerset .left .input{
      width: 100%;
      margin-bottom: 20px;
      border: none;
      height: 40px;
      padding-top: 10px;
    }
    .uploadset .inerset .left .input select{
      border: none;
    }
    .Questing .inquest .questing1{
      flex-direction: column;
      align-items: baseline;
      margin-bottom: 15px;
    }
    .Questing .inquest .questing1 p{
      margin-bottom: 10px;
    }
    .editor #btn{
      width: 100%;
    }
    .Questing .inquest .questing1 form {
      width: 100%;
    }
    .Questing .inquest .questing1 form input[type='text']{
      width: 100%;
      border: 1px solid #E0E0E0;
      height: 35px;
    }
  }

  /* upload section */
  .uplcenter{
    display: flex;
    justify-content: space-between;
  }
  .uplcenter .imageupload{
    width: 30%;
    display: flex;
    flex-direction: column;
    row-gap:15px;
  }
  .uplcenter .imageupload .imgup{
    position: relative;
    border: 1px solid #E0E0E0;
  }
  .uplcenter .imageupload .imgup img{
   width: 100%;
   object-fit: contain;
  }
  .uplcenter .imageupload .imgup h2{
    width: 100%;
    height: 28px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    background: rgba(0, 0, 0, 0.6);
    text-align: center;
    color: white;
    position: absolute;
    bottom: -6px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

 
  .uplcenter .imageupload .btnup button{
    width: 100%;
    height: 38px;
    background: #FAFAFA;
    border: 1px solid #4F4F4F;
  }
  .uplcenter .uploadedimg {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-column-gap: 05px !important;
  }
  .uplcenter .uploadedimg .uplsecimg {
    width: 100%;
    position: relative;
    height: 120px;
    margin-bottom: 15px;
  }
  .uplcenter .uploadedimg .uplsecimg img{
    width: inherit;
  }
