
  .gallery_main .gallery {
    width: 100%;
    margin-top: 15px;
    height: auto;
    display: flex;
    justify-content: end;
  }
  .gallery_main .gallery .left {
    width: 30%;
    border: none;
    padding-left: 12px;
  }
  .gallery_main .gallery .left .image img {
    width: 300px;
    height: 300px;
    object-fit: cover;
  }
  .gallery_main .gallery .left .form {
    width: 360px;
    height: auto;
    padding: 1.5rem 0.1rem;
  }
  .gallery_main .gallery .left .form .inputs {
    width: 96.5%;
    height: 30px;
    margin-top: 1rem;
    display: flex;
  }
  .gallery_main .gallery .left .form .inputs .input {
    width: 90%;
    height: 32.5px;
    background-color: white;
    border: 1px solid black;
    border-radius: 3%;
  }
  .gallery_main .gallery .left .form .inputs .input a {
    width: 5%;
  }
  .gallery_main .gallery .left .form .inputs .input select{
    border: none;
    width: 78.5%;
    height: 25px;
    outline: none;
    padding: 0rem 0.3rem;
    font-size: 15px;
  }
  .gallery_main .gallery .left .form .inputs .input input {
    border: none;
    width: 75%;
    height: 25px;
    outline: none;
    padding: 0rem 0.3rem;
    font-size: 15px;
  }
  #fileinput{
    height: 32.5px;
  }
  #imginput {
    width: 78.48%;
  }
  .gallery_main .gallery .left .form .inputs .input #btn {
    width: 19.5%;
    height: 29px;
    margin-top: 1px;
    margin-left: 05px;
  }
  .gallery_main .gallery .left .form .inputs .delete {
    width: 28px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.4rem;
    margin-right: 0.4rem;
    border-radius: 50%;
    border: 1px solid black;
  }
  .gallery_main .gallery .left .form .inputs .delete:hover {
    cursor: pointer;
  }
  .gallery_main .gallery .center {
    width: 42%;
    height: 420px;
    display: grid;
    align-items: flex-start;
    justify-content: center;
    row-gap: 5px;
    column-gap: 2px;
    padding: 0 0px;
  }
  .gallery_main .gallery .center .image {
    width: 100px;
    height: 100px;
    border: 0.5px solid #cac8cc;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    overflow: hidden;
  }
  .gallery_main .gallery .center .image img {
    width: 100px;
    height: 100px;
  }
  .gallery_main .gallery .center .image p {
    width: 100px;
    height: 20px;
    background: rgba(0, 0, 0, 0.6);
    font-family: Times New Roman;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
  }
  .gallery_main .gallery .right {
    width: 23%;
  }
  .gallery_main .gallery .right #id {
    display: none;
  }
  .gallery_main .gallery .right .title {
    width: 95%;
    height: auto;
    margin-bottom: 05px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: Arial;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    letter-spacing: 0.05em;
    color: #000000;
  }
  input {
    background-color: transparent !important;
  }
  .gallery_main .gallery .right .title .type {
    background: #f4f4f4;
    border: 1px solid #494848;
    border-radius: 3px;
    width: 45px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0%;
  }
  #btun,
  #btun2 {
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  .gallery_main .gallery .right .title .filname {
    font-family: Times New Roman;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0.1em;
    color: #494848;
    margin: 0 10px 0 10px;
    padding-right: 0px;
    width: 50%;
  }
  .gallery_main .gallery .right .title .fas {
    padding: 0.25rem;
    border: 0.5px solid black;
    border-radius: 50%;
    cursor: pointer;
  }
  /* preview images */
.allimages .row1 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    grid-gap: 6px;
    margin-bottom: 6px;
    align-items: flex-start;
    justify-content: center;
  }
  .allimages .row1 .col1,
  .allimages .row1 .col2,
  .allimages .row1 .col3,
  .allimages .row1 .col4 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    overflow: hidden;
    border: 0.5px solid #cac8cc;
  }
  
  .allimages .row1 .col1 img,
  .allimages .row1 .col2 img,
  .allimages .row1 .col3 img,
  .allimages .row1 .col4 img {
    width: 100px;
    height: 100px;
    margin-bottom: 0;
    object-fit: cover;
  }
  .allimages .row1 .col1 p,
  .allimages .row1 .col2 p,
  .allimages .row1 .col3 p,
  .allimages .row1 .col4 p {
    width: 100px;
    height: 20px;
    background: rgba(0, 0, 0, 0.6);
    font-family: "Times New Roman", Times, serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    letter-spacing: 0.2em;
    line-height: 1.3;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center;
    margin-top: -20px;
    margin-bottom: 0%;
    padding-top: 0px;
  }

  @media (max-width:769px) {
    .gallery_main{
      width: 90%;
    }
    
    .gallery_main .gallerypart{
      flex-direction: column;
    }
  
    .uplcenter .uploadedimg{
      width: 100% !important;
      grid-template-columns: repeat(2,1fr) !important;
      grid-gap: 0px;
      margin-top: 20px;
    }
    .uplcenter{
      flex-direction: column;
    }
    .uplcenter .imageupload{
      width: 100% !important;
    }
    .uplcenter .uploadedimg .uplsecimg{
      height: 150px !important;
      margin-top: 5px;
    }
    .uploadset .inerset .left .categ{
      flex-direction: column;
    }
    .uploadset .inerset .left .input{
      width: 100%;
      margin-bottom: 20px;
      border: none;
      height: 40px;
      padding-top: 10px;
    }
    .uploadset .inerset .left .input select{
      border: none;
    }
    .Questing .inquest .questing1{
      flex-direction: column;
      align-items: baseline;
      margin-bottom: 15px;
    }
    .Questing .inquest .questing1 p{
      margin-bottom: 10px;
    }
    .editor #btn{
      width: 100%;
    }
    .Questing .inquest .questing1 form {
      width: 100%;
    }
    .Questing .inquest .questing1 form input[type='text']{
      width: 100%;
      border: 1px solid #E0E0E0;
      height: 35px;
    }

    .uplcenter .uploadedimg .uplsecimg img{
      height: 150px !important;
    }
  }

  /* upload section */
  .uplcenter{
    display: flex;
    justify-content: space-between;
  }
  .uplcenter .imageupload{
    width: 30%;
    display: flex;
    flex-direction: column;
    row-gap:15px;
  }
 
  .uplcenter .imageupload .imgup{
    position: relative;
  }
  .uplcenter .imageupload .imgup img{
   height: 300px;
  }
  .uplcenter .imageupload .imgup h2{
    width: 100%;
    height: 28px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    background: rgba(0, 0, 0, 0.6);
    text-align: center;
    color: white;
    position: absolute;
    bottom: -6px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .uplcenter .imageupload .btnup label{
    width: 100%;
    height: 38px;
    background: #FAFAFA;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #4F4F4F;
  }
  /* .uplcenter .imageupload .btnup label button{
    width: 100%;
    height: 38px;
    background: #FAFAFA;
    border: 1px solid #4F4F4F;
  } */
  .uplcenter .imageupload .btnup label input{
    background-color: transparent;
  }
  .uplcenter .uploadedimg {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-column-gap: 05px !important;
  }
  .uplcenter .uploadedimg .uplsecimg {
    width: 100%;
    position: relative;
    height: 120px;
    margin-bottom: 5px !important;
  }
  .uplcenter .uploadedimg .uplsecimg img{
    width: inherit;
    height: 120px;
  }
  .uplcenter .uploadedimg .uplsecimg h6{
    width: 100%;
    height: 28px;
    font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;
display: flex;
background: rgba(0, 0, 0, 0.6);
color: white;
justify-content: center;
align-items: center;
position: absolute;
bottom: 0px;
  }
  .uplcenter .uploadedimg .uplsecimg span{
      position: absolute;
      top:05px;
      right: 10px;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      color: white;
      background-color: rgba(0, 0, 0, 0.6);
      font-size: 12px;
  }

  .btn_div{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0px;
    margin-bottom: 5rem;
  }
  #btn {
    margin-top: 47px;
    width: 84px;
    height: 38px;
    cursor: pointer;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #ffffff;
    outline: none;
    background:  #4F4F4F;
    border-radius: 3px;
    text-transform: capitalize;
    align-self: baseline;
    text-align: center;
  }