.completeprof{
  padding: 100px 0 100px 0;
  background-color: #f4f4f4;
}
.completeprof .login_heading{
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #494848;
  text-align: center;
}
.completeprof .completeform{
  width: 70%;
  margin: 0 auto;
  padding: 30px 0;
}
.completeprof .completeform .formpati{
  display: flex;
}
.completeprof .completeform .formpati .formp1,.completeprof .completeform .formpati .formp2{
  width: 50%;
}
.completeprof .completeform .formpati .formp1 h6{
  font-family: 'Calibri';
font-style: normal;
font-weight: 700;
font-size: 22px;
color: #494848;
margin-bottom: 0;
}
.completeprof .completeform .formpati .formp1 p{
  font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
color: #494848;
margin: 010px 0;
}
#company,#country{
  margin-top: 5px;
}
.completeprof .completeform .formpati .formp1 .fcinput label,.completeprof .completeform .formpati .formp2 .inputs label{
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  margin-top: 25px;
  margin-bottom: 10px;
}
.completeprof .completeform .formpati .formp1 .fcinput select,.completeprof .completeform .formpati .formp2 .inputs input{
  width: 90%;
  border: 1px solid #E0E0E0;
  background-color: #ffffff !important;
  height: 33px;
  padding-left: 10px;
  outline: none;
}
.completeprof .completeform .formpati .formp2 .inputs input{
  width: 100%;
}
.completeprof .completeform .formpati .formp1 .verifyprofile{
  margin-top: 20px;
}
.completeprof .completeform .formpati .formp1 .verifyprofile p{
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #494848;
  margin-bottom: 05px;
}
.completeprof .completeform .formpati .formp1 .verifyprofile label{
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  margin: 5px 25px 0 8px;

}
.completeprof .completeform .formpati .formp2 .inputs label span{
  opacity: 0.5;
}
.completeprof .completeform .formpati .formp2 .addres{
  display: flex;
}
.completeprof .completeform .formpati .formp2 .addres .inputs:first-child{
  width: 75%;
  padding-right: 2.5%;
}
.completeprof .completeform .formpati .formp2 .addres .inputs input{
  width: 100%;
}
.completeprof .completeform .formpati .formp2 .addres .inputs:last-child{
  width: 25%;
  padding-left: 2.5%;
}
.completeprof .completeform .formpati .formp2 .smsphone{
  display: flex;
  margin-bottom: 25px;
}
.completeprof .completeform .formpati .formp2 .smsphone .inputs{
  width: 70%;
}
.completeprof .completeform .formpati .formp2 .smsphone .btns{
  width: 30%;
  padding-left: 2.5%;
  align-self: flex-end;
}
.completeprof .completeform .formpati .formp2 .smsphone .btns button{
  width: 100%;
height: 38px;
background: #FAFAFA;
border: 1px solid #4F4F4F;
font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
color: #4F4F4F;
}

.completeprof .completeform .formpati .formp2 .submitsms{
  display: flex;
  margin-bottom: 25px;
}
.completeprof .completeform .formpati .formp2 .submitsms .inputs{
  width: 70%;
}
.completeprof .completeform .formpati .formp2 .submitsms .btns{
  width: 30%;
  display: flex;
  justify-content: end;
}

#comacount{
  width: 84px;
  height: 38px;
  background-color: #4F4F4F !important;
  color: #ffffff;
}

@media (max-width:769px) {
  .completeprof{
    padding: 40px 0;
  }
  .completeprof .login_heading{
    font-size: 24px;
    text-align: left;
  }
  .completeprof .completeform{
    width: 100%;
    padding: 20px 25px;
  }
  .completeprof .completeform .formpati{
    flex-direction: column;
  }
  .completeprof .completeform .formpati .formp1, .completeprof .completeform .formpati .formp2{
    width: 100%;
  }
  .completeprof .completeform .formpati .formp2{
    padding-top: 50px;
  }
  .completeprof .completeform .formpati .formp2 .addres .inputs:first-child{
    width: 60%;
  }
  .completeprof .completeform .formpati .formp2 .addres .inputs:last-child{
    width: 40%;
  }
  .completeprof .completeform .formpati .formp2 .smsphone{
    flex-direction: column;
  }
  .completeprof .completeform .formpati .formp2 .smsphone .inputs{
    width: 100%;
  }
  .completeprof .completeform .formpati .formp2 .smsphone .btns{
    align-self: auto;
    width: 40%;
    margin-top: 16px;
    padding-left: 0;
  }
  .completeprof .completeform .formpati .formp2 .submitsms{
    flex-direction: column;
  }
  .completeprof .completeform .formpati .formp2 .submitsms .inputs{
    width: 100%;
  }
  .completeprof .completeform .formpati .formp2 .submitsms .btns{
    margin-top: 16px;
    padding-left: 0%;
    justify-content: flex-start;
  }
}



