
.Login{
  padding: 100px 0;
  background-color: #f4f4f4;
}
.login_heading {
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #494848;
  text-align: center;
}
.loginform{
  width: 30%;
  margin: 0 auto;
}
.loginform .inputs label{
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  margin-top: 25px;
  margin-bottom: 10px;
}
.loginform .inputs input{
  width: 100%;
  border: 1px solid #E0E0E0;
  background-color: #ffffff !important;
  height: 33px;
  padding-left: 10px;
}
.loginform .loginaction{
  margin: 25px 0;
  display: flex;
}
.loginform .loginaction .btns{
  width: 30%;
}
.loginform .loginaction .btns .submit{
  width: auto;
  height: auto;
  padding: .5rem 1.2rem;
  background: #4F4F4F !important;
  border: none;
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #ffffff;
}
.loginform .loginaction .fpassword{
  width: 60%;
}
.loginform .loginaction .fpassword p{
  height: 38px;
  display: flex;
  align-items: center;
  font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
color: #494848;
margin-left: 30px;
}
.loginform .regnow h3{
  font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
color: #494848;
}
.loginform .regnow .reglogin{
  color: #494848;
}
@media (max-width:769px) {
  .Login{
    padding: 50px 0;
  }
  .login_heading{
    text-align: left;
    padding: 0 20px;
    font-size: 24px;
  }
  .loginform{
    width: 100%;
    padding: 0 20px;
  }
  .loginform .loginaction{
    flex-direction: column;
  }
  .loginform .loginaction .btns{
    width: 100%;
  }
  .loginform .loginaction .fpassword p{
    width: 199px;
    height: 38px;
    justify-content: center;
    border: 1px solid #4F4F4F;
    margin-left: 0%;
    margin-top: 15px;
  }
}