.containers {
  padding-top: 150px;
}
.topart {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-left: 5%;
}

.topart .salogans {
  width: 40%;
}
.topart .salogans h6 {
  font-size: 18px;
  font-weight: 300;
  color: #000000;
  padding-left: 80px;
}
.headig {
  text-align: center;
  padding: 20px 0;
  margin-top: 20px;
  border-top: 0.5px solid #9c9c9c;
}
.headig h1 {
  font-size: 18px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.topart .prfile {
  width: 60%;
}

.topart .prfile h2 {
  font-size: 24px;
  font-weight: 300;
  padding-top: 20px;
  letter-spacing: 2px;
}
.topart .prfile img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
}
.prfiles {
  display: flex;
  justify-content: space-evenly;
  padding-left: 3%;
}
.prfiles .save1 {
  width: 400px;
  height: 400px;
}
.collections {
  width: 400px;
  height: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
}
.collections .save1,
.collections .save2 {
  display: flex;
  justify-content: space-between;
}
.collections .save1 img,
.collections .save2 img {
  width: 198px;
  height: 198px;
}
.collname {
  width: 100%;
  position: absolute;
  bottom: 0%;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  padding: 15px 0;
}
.collname h3 {
  padding: 25px auto;
  font-size: 24px;
  color: #ffffff;
}
