
.mainpartgallery0 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:50px;
    margin-top: 80px;
}
.partgallery{
    padding: 100px 50px;
    background-color: #f4f4f4;
}
.partgallery .designheading h1{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    color: #494848;
    text-align: center;
    margin-bottom: 46px;
}
.partgallery .partsort{
    margin-bottom: 20px;
}
.partgallery .partsort form{
    height: 40px;
    display: flex;
    align-items: center;
}
.partgallery .partsort form label{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #828282;
    padding-right: 30px;
}
.partgallery .partsort form input{
    width: 250px;
    height: 38px;
    background-color: #FFFFFF !important;
    border: 1px solid #F2F2F2;
    padding : 5px 10px;
    outline: none;
}
  .designpart {
display: grid;
}
  .designpart .mainimage {
  position: relative;
  width: inherit;
}
 .designpart .mainimage img{
    width:100%;
    height: 400px;
    object-fit: cover;
    object-position: center;
}
  .designpart .mainimage .imagehover{
    position: absolute;
    height: 400px;
    width: 100%;
    top: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.216) 28.74%, rgba(0, 0, 0, 0) 53.89%, rgba(0, 0, 0, 0.318) 77.4%, rgba(0, 0, 0, 0.6) 100%);
    backdrop-filter: blur(2px);
    display: none;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 20px;
}
  .designpart .mainimage:hover .imagehover{
    display: flex;
}
  .designpart .mainimage .imagehover .hoverhead{
    display: flex;
    justify-content: space-between;
    height: 48px;
    align-items: center;
}
  .designpart .mainimage .imagehover .infosvg{
    display: flex;
    align-items: center;
}
  .designpart .mainimage .imagehover .infosvg h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    padding-left: 5px;
}
  .designpart .mainimage .imagehover .hoverhead .hoverheadright{
    display: flex;
    align-items: center;
}
  .designpart .mainimage .imagehover .hoverhead .hoverheadright .bookmark{
    padding-left: 15px;
}
  .designpart .mainimage .imagehover .hoverbottom{
    display: flex;
    height: 45px;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
}
  .designpart .mainimage .imagehover .hoverbottom .hbottoml h4{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    color: #F2F2F2;
}
  .designpart .mainimage .imagehover .hoverbottom .hbottomr{
    display: flex;
}
  .designpart .mainimage .imagehover .hoverbottom .hbottomr .icons{
    padding-left: 10px;
}
  .designpart .partbottom{
    width: 100%;
    display: flex;
    background-color: #FFFFFF;
    height: 54px;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    margin-top: -5px;
}
  .designpart .partbottom .partauthor h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #4F4F4F;
}
  .designpart .partbottom  .particons{
    display: flex;
}
  .designpart .partbottom .particons .dicons{
    display: flex;
    padding-right: 20px;
    align-items: center;
}
  .designpart .partbottom  .dicons:last-child{
    padding-right: 0;
}
  .designpart .partbottom .particons .dicons h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #4F4F4F;
    padding-left: 8px;
}
.popup-content {
    width: 100%;
    height: 100vh;
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    padding: 3vh 100px 0px 100px;
    background-color: rgba(0,0,0,0.6);
    z-index: 1;
}
  /* popupclose class */
  .close{
    position: fixed;
    top: 0;
    right: 30px;
    font-size: 42px;
    color: white;
    cursor: pointer;
    text-decoration: none;
}
