.mobilelayouts{
    display: none;
    justify-content: space-between;
}
.mobilelayouts .mobilesorting h3{
    font-size: 20px;
}
.mobilelayouts .mobilesorting h3 svg{
    margin-right: 10px;
}
.mobilelayouts .difflayouts{
    display: flex;
}
.mobilelayouts .difflayouts .layimg{
    margin-left: 10px;
}
@media (max-width:769px) {
    .partgallery{
        padding: 50px 20px !important;
    }
    .partgallery .partsort form{
     flex-direction: column;
     align-items: flex-start !important;
     height: auto !important;
    }
    .partgallery .partsort form label{
        margin-bottom: 10px;
    }
    .partgallery .partsort form input{
        margin: 10px 0px;
        width: 100% !important;
    }
    .mainpartgallery0{
        grid-template-columns: 1fr;
    }
    .partgallery .partsort{
        display: none;
    }
    .mainpartgallery0 {
        grid-template-columns: 1fr;
        grid-gap:20px;
    margin-top: 80px;
    }
    .mainpartgallery1 {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap:20px;
    margin-top: 80px;
    }
    .mainpartgallery2 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap:15px;
    margin-top: 80px;
    }
    .mainpartgallery2 .designpart .partbottom,.mainpartgallery3 .designpart .partbottom{
        display: none;
    }
    .mainpartgallery2 .designpart .mainimage img{
        height: 155px;
    }
    .mainpartgallery3 .designpart .mainimage img{
        height: 100px;
    }
    .mainpartgallery2 .designpart .mainimage .imagehover, .mainpartgallery3 .designpart .mainimage .imagehover{
        display: none;
    }
    .mainpartgallery3 {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap:15px;
    margin-top: 80px;
    }
    .mobilelayouts{
        margin-bottom: 40px;
        display: flex;
    }
    .designpart .mainimage img{
        height: 300px;
    }
    .designpart .mainimage .imagehover{
        height: 300px;
    }
}