*{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}
.header{
    display: flex;
    justify-content: space-between;
    padding: 0px 50px;
    height: 100px;
    align-items: center;
    background-color: #F4F4F4;
    border-bottom: 1px solid #E0E0E0;
}
.logo-menu{
    text-decoration: none;
}
.header .logo img{
    width: 234.13px;
height: 18px;
}
.header .logo h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: #333333;
    margin-top: 10px;
    letter-spacing: 0.1em
}
.header .headermenu{
    display: flex;
}
.header .headermenu .headersearch form input{
    width: 250px;
padding: 5px 16px;
background-color:  #ffffff !important;
border: 1px solid #E0E0E0;
outline: none;
}
.header .headermenu  .language{
    padding: 0 5px 0 15px;
    display: flex;
    align-items: center;
}

.header .headermenu  .language select{
    border: none;
    outline: none;
    background-color: transparent;
    color: #828282;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
}
.header .headermenu .profile {
    padding: 0 15px;
    cursor: pointer;
}
.header .headermenu .profile img{
    display: none;
}
.header .headermenu .profile button{
    background-color: #4F4F4F;
    padding: 8px 16px;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
}
.header .headermenu .hammenu{
    padding: 0 15px;
}
.header .headermenu .hammenu img{
    width: 30px;
    height: 30px;
}
.header .headermenu .hammenu svg{
    cursor: pointer;
}
#mobileviewmn{
    display: none;
}
@media (max-width:769px) {
    .header{
        justify-content: start;
        padding: 0 20px;
    }
    .header .headermenu .headersearch {
        display: none;
    }
    .header .logo h6{
        font-size: 9.5px;
        letter-spacing: normal;
    }
    .header .headermenu .language{
        display: none;
    }
    .header .logo{
        width: 85%;
    }
    .register .register_heading{
        font-size: 24px !important;
        text-align: left;
    }
    .header .headermenu .profile{
        padding: 0 0 0 25px;
    }
    #mobileviewmn{
        display: block;
        background-color: #828282;
        color: white;
        padding-top: 20px;
        padding-bottom: 50px;
    }
    #mobileviewmn .navlink{
        color: white;
    }
    .sidemnu ul{
        padding-top: 20px;
    }
    .sidemnu ul li{
        height: 45px !important;
    }
    .header .headermenu .profile img{
        display: block;
    }
    .header .headermenu .profile button{
        display: none;
    }
}
