.mfaq{
    text-align: center;
    padding-bottom: 50px;
    padding-top: 100px;
}
.mfaq h1{
    font-size: 38px;
    text-transform: capitalize;
    padding-bottom: 70px;
}
.mfaq .mqa .qhead {
    width: 75%;
    margin-top: 25px;
    margin: 15px auto;
}
.mfaq .mqa .qhead h2{
    padding: 15px 0px 15px 30px;
    background-color: rgb(235, 235, 235);
    font-size: 20px;
    font-weight: 500;
    display: flex;
    align-items: center;
    margin-bottom: 0;
    cursor: pointer;
}
.mfaq .mqa .qhead h2:hover{
    color: grey;
}
.mfaq .mqa .qhead h2 i{
    font-size: 30px;
    font-weight: 200;
    padding-right: 15px;
}
.mfaq .mqa .qhead .manswer{
    padding: 10px 30px;
    background-color: white;
    display: none;
}
.mfaq .mqa .qhead .manswer p{
    font-size: 17px;
    font-weight: 400;
    opacity: 0.8;
    text-align: left;
}
@media (max-width:750px) and (min-width:460px){
    .mfaq .mqa .qhead{
        width: 90%;
    }
    .mfaq .mqa .qhead h2{
      font-size: 18px;
    }
}
@media (max-width:460px){
    .mfaq .mqa .qhead{
        width: 95%;
    }
    .mfaq .mqa .qhead h2{
      font-size: 14px;
      padding-right: 15px;
      text-align: left;
    }
}