#chatpage{
  padding-top:50px;
  padding-bottom:50px;
  background-color:#f4f4f4;
}
#chatrows{
  display: flex;
}
#chatrows .chatlistusers,.nochatlistusers{
  width: 30%;
}
#chatrows .chats,.nochats{
  width: 70%;
}
.message-bottom-bar {
  padding: 10px 20px 10px 0;
  height: 60px;
  display: flex;
  background-color: #F4F4F4;
  align-items: center;
}



.main-message:focus{
  background-color: silver;
}
/* .main-message{
  background-color: silver;
} */




#message-container{
  height: 490px;
  overflow-y: scroll;
}
.msgput {
  margin-right: 15px;
}
.messages-list::-webkit-scrollbar {
  width: 6px;
  height: 6%;
}
.messages-list {
  height: auto;
  overflow-y: scroll;
  padding: 10px 15px 10px 25px;
}
.messages-list::-webkit-scrollbar-track {
  background-color: transparent;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  height: 90%;
}
.messages-list::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  height: 10% !important;
  background: #6d6d6d;
}
.p0 {
  padding: 0 5px 0 0 !important;
}
.user-chatlist::-webkit-scrollbar {
  width: 6px;
  height: 6%;
}
.user-chatlist::-webkit-scrollbar-track {
  background-color: transparent;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  height: 90%;
}
.user-chatlist::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  height: 10% !important;
  background: #6d6d6d;
}
.msgput{
  height: 38px;}

.msgput input {
  background-color: white !important;
  border: 0.1px solid #494848;
  border-radius: 3px;
  height: 38px;
  outline: none;
}
.h50 {
  height: 600px;
}
.h30 {
  height: 30%;
}
/*  */
.h60 {
  border-top: 1px solid rgba(0, 0, 0, 0.25);
}
.py2 {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

.mt0 {
  padding: 13px 0 0 0;
  margin: 0 0px;
  border-top: 1px solid rgba(0, 0, 0, 0.25);
}
.user-chatlist {
  height: 455px;
  overflow: scroll;
}
.list-unstyled{
  padding-top: 20px;
  display: flex;
  flex-direction: column;
}
.messagedev{
  display: block;
  margin-top: 10px;
}
.messagedev .message-box{
 padding: 10px 25px 20px 10px;
border-radius: 5px;
width: auto;
display: inline-block;
max-width: 60%;
font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 18px;
}
.messagedev:nth-child(odd) .message-box{
  background-color: rgba(172, 130, 130, 0.22);
} 
 /* .messagedev:nth-child(even){
  justify-content: flex-end;
  display: flex;
} */
.messagedev .left_msg{
  float: left!important;
  background-color: #F4F4F4!important;
}
.messagedev .right_msg{
  float: right!important;
}
#datetoset{
  margin: 15px 0;
}
.messages-list{
  overflow: scroll;
}
#btnsemd{
  position: absolute;
  right: 2%;
  top: 5px;
}
.bactolist h5{
  color: #494848;
  padding: 10px 0 10px 20px;
  margin-bottom: 0;
  display: none;
  font-family: 'Calibri';
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
}
.bactolist h5 svg{
  margin-right: 10px;
}
@media (max-width:769px) {
  .bactolist h5{
    display: block;
  }
  #chatpage{
    padding-top:0px;
    padding-bottom:0px;
  }
  #chatcontainer{
    width: 100%;
    padding: 0%;
  }
  #chatrows .chatlistusers,.nochatlistusers{
    width: 100%;
  }
  #chatrows .chats,.nochats{
    width: 100%;
  }
  #chatrows .chats{
   display: none;
  }
  .nochatlistusers{
    display: none;
  }
  .messagedev .message-box{
    max-width: 95%;
  }
  .messagedev{
    margin-top: 15px;
  }
  #backtoclist{
    padding: 10px 0;
  }
  .msgput input{
    border: none;
  }
}