* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.setting_main {
  width: 100%;
  height: auto;
  padding: 150px 2rem 5rem 5rem;
  display: grid;
  grid-template-columns: 0.3fr 0.7fr;
}
.input_box_register input[type="text"],
select {
  margin: 0 !important;
}
.setting_main .setting-left .img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.setting_main .setting-left .img-div .imgset {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.setting_main .setting-left .img-div .imgset .imag {
  width: 63px;
  height: 63px;
  background: #ffffff;
  border: 0.5px solid #494848;
  border-radius: 50%;
  margin-bottom: 15px;
  overflow: hidden;
}
.setting_main .setting-left .img-div .imgset .imag img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.setting_main .setting-left .img-div .imgset h6 {
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  letter-spacing: 0.2em;
  color: #000000;
  padding-left: 10px !important;
}
.setting_main .setting-left .img-div .name {
  padding-left: 30px;
}
.setting_main .setting-left .img-div .name h2 {
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  letter-spacing: 0.2em;
  color: #000000;
  margin-bottom: 30px;
}
.setting_main .setting-left .info-form form textarea {
  width: 328px;
  height: 198px;
  background: #ffffff;
  border: 0.5px solid #494848;
  border-radius: 3px;
}
.setting_main .setting-left .info-form form input {
  width: 328px;
  height: 35px;
  background: #ffffff;
  border: 0.5px solid #494848;
  border-radius: 3px;
}
.setting_main .setting-left .info-form form p {
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 0.1em;
  color: #000000;
  margin-bottom: 30px;
}
.setting_main .setting-left .info-form {
  margin-top: 50px;
}
.setting_main .setting-right {
  padding-top: 70px;
}
.setting_main .setting-right form .inputs {
  width: 100%;
  display: flex;
  margin-bottom: 40px;
}
.setting_main .setting-right form .inputs .label {
  width: 40%;
  text-align: right;
}
#pflabel {
  padding-top: 10px;
}
.setting_main .setting-right form .inputs .label label {
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.2em;
  color: #000000;
  text-transform: uppercase;
}
.setting_main .setting-right form .inputs input {
  width: 60%;
  height: 30px;
  background: #ffffff;
  border: 0.5px solid #494848;
  margin-left: 30px;
}
.switch-button {
  background: rgba(255, 255, 255, 0.56);
  border-radius: 30px;
  overflow: hidden;
  width: 280px;
  text-align: center;
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  letter-spacing: 0.2em;
  color: #000000;
  position: relative;
  padding-right: 120px;
  position: relative;
  border: 1px solid #494848;
  margin-left: 25%;
}
.switch-button:before {
  content: "not verified";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 010px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  pointer-events: none;
  padding-left: 100px;
}

.switch-button-checkbox {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
}
.switch-button-checkbox:checked + .switch-button-label:before {
  transform: translateX(120px);
  transition: transform 300ms linear;
}

.switch-button-checkbox + .switch-button-label {
  position: relative;
  padding: 10px 0;
  display: block;
  user-select: none;
  pointer-events: none;
}
.switch-button-label:before {
  content: "";
  background: rgba(0, 0, 0, 0.65);
  border-radius: 0px 0px 0px 0px;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(0);
  transition: transform 300ms;
}

.switch-button-label-span {
  position: relative;
}
.setting_main .setting-right form .inputs .input {
  width: 30%;
  margin-left: 30%;
  display: flex;
  background: #ffffff;
  justify-content: end;
  align-items: center;
  border: 0.5px solid #494848;
}
.setting_main .setting-right form .inputs .input input {
  width: 90%;
  border: none;
}
.setting_main .setting-right form .inputs .input i {
  width: 10%;
}
.note {
  font-family: Calibri;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.1em;
  color: #000000;
}
.fainputs {
  padding-left: 65px;
}
#fa {
