.side_bar ul{
    list-style: none;
    margin-top: 30px;
}
.side_bar ul li{
    padding: 25px 0px;
}
.dashsidelink{
    font-size: 22px;
    font-weight: 500;
    color: #828282;
    cursor: pointer;
    text-decoration: none;
}
.side_bar{
    border-right:1px solid #eae5e5;
    min-height: 90vh;
    position: static;
}
.custom-flex-gap{
    gap: 20px;
    flex-wrap: wrap;
    padding: 30px 20px;
}
.myusercardlink{
    width: 30%;
    text-decoration: none;
}
.myusercardlink a{
    text-decoration: none;
}
.myusercard{
    width: 100%;
    height: 200px;
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}
.myusercard svg{
    font-size: 28px;
}
.myusercard .textside h2{
    font-size: 26px;
    color: #505050;
}
.myusercard .textside p{
    font-size: 24px;
    color: #828282;
}

