/* add user */

@font-face {
    font-family: Manrope;
    src: url(../font/Manrope-Regular.ttf);
  }
.adduser{
    font-family: 'Calibri';
font-style: normal;
font-weight: 400;
font-size: 36px;
color: #494848;
text-align: center;
}
.gender{
    width: 80%;
    margin: 0 auto;
    height: 30px;
    display: flex;
    align-items: center;
    padding-top: 65px;
}
.gender label{
    margin-left: 8px;
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #212121;
}
.gender #female{
    margin-left: 25px;
}
.register-fields{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
.register-fields .fields1{
    width: 50%;
    margin: 0 auto;
}
.register-fields .fields1:first-child{
    padding-right: 2.5%;
}
.register-fields .fields1:last-child{
    padding-left: 2.5%;
}
.register-fields .fields1 label{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    margin-top: 25px;
    margin-bottom: 10px;
}
.register-fields .fields1 input{
    width: 100%;
    border: 1px solid #E0E0E0;
    background-color: #ffffff !important;
    height: 33px;
    padding-left: 10px;
}
.signupbtn{
    width: 80%;
    margin: 40px auto; 
    text-align: center;
}
.signupbtn .submit{
    width: 84px;
    height: 38px;
    background: #4F4F4F !important;
    border: none;
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
}
.signupbtn .nosignup{
    margin-top: 10px;
}
.signupbtn .nosignup h3{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #494848;
}
.signupbtn .nosignup .reglogin{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: #494848;  
}

@media(max-width:769px){
    .gender,.register-fields{
        margin: 0;
    }
    .register-fields{
        flex-direction: column;
        width: 100%;
    }
    .register-fields .fields1{
        width: 100%;
    }
    .signupbtn{
        margin: 40px 0;
        text-align: left;
    }
    .register-fields .fields1:last-child{
        padding-left: 0%;
    }
}

